<template>
  <VTextFieldNumber
      label="Map Center Lat"
      v-model="widget.properties.options.location.latitude"
  ></VTextFieldNumber>
  <VTextFieldNumber
      label="Map Center Lon"
      v-model="widget.properties.options.location.longitude"
  ></VTextFieldNumber>
  <VTextFieldNumber
      label="Map Center Alt"
      v-model="widget.properties.options.location.altitude"
  ></VTextFieldNumber>
  <VTextFieldNumber
      label="Map Orientation Heading"
      v-model="widget.properties.options.orientation.heading"
  ></VTextFieldNumber>
  <VTextFieldNumber
      label="Map Orientation Pitch"
      v-model="widget.properties.options.orientation.pitch"
  ></VTextFieldNumber>
</template>

<script>
import {inject} from "vue";
import VTextFieldNumber from "@/components/VTextFieldNumber";

export default {
  name: "WidgetFormCesiumMap",
  setup(props) {
    const widget = inject('widget');
    if(!widget.properties.options) {
      widget.properties.options = {};
    }
    if(!widget.properties.options.location) {
      widget.properties.options.location = {
        longitude: undefined,
        latitude: undefined,
        altitude: undefined
      };
    }

    if(!widget.properties.options.orientation) {
      widget.properties.options.orientation = {
        pitch: undefined,
        heading: undefined,
      };
    }

    widget.properties.options.layers = ['Bing Maps Aerial', 'Bing Maps Aerial with Labels', 'Bing Maps Roads'];

    return { widget };
  },
  components: {
    VTextFieldNumber
  }
}
</script>

